<template>
    <div class="nav">
        <article>
            <!--Блок с лого-->
            <img class="logoHeader" src="../assets/header/logo-white.png" />
        </article>
        <a href="#" class="nav__trigger" @click.prevent="isActive = !isActive">
            <div class="bars"></div>
        </a>
        
        <div class="nav__content" :class="{active: isActive}">
            <nav class="nav__list">
                <ul>
                    <li class="nav__item"><router-link tag="a" to="/">ГЛАВНАЯ</router-link></li>
                    <li class="nav__item"><router-link tag="a" to="/product" href="#">ПРОДУКТЫ</router-link></li>
                    <li class="nav__item"><router-link tag="a" to="/production" href="#">ПРОИЗВОДСТВО</router-link></li>
                    <li class="nav__item"><router-link tag="a" to="/contacts" href="#">КОНТАКТЫ</router-link></li>
                </ul>
            </nav>
        </div>    
    </div>
</template>

<script>
export default {
    data() {
        return {
            isActive: false,
        }
    }
}
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 815px) {
    @function random-color($min: 0, $max: 255, $alpha: 1, $red: null, $green: null, $blue: null) {
    @if $min < 0 {
        $min: -1;
    } @else {
        $min: $min - 1;
    }
    @if $max > 255 {
        $max: 256;
    } @else {
        $max: $max + 1;
    }

    $range: $max - $min;
    @if not $red { $red: random($range) + $min; }
    @if not $green { $green: random($range) + $min; }
    @if not $blue { $blue: random($range) + $min; }

    @return rgba($red, $green, $blue, $alpha);
}

// Colors
$primary: #fff;
$primary-ultralight: mix(white, $primary, 80%);
$primary-light: mix(white, $primary, 25%);
$primary-dark: mix(black, $primary, 25%);
$primary-ultradark:  #161612 ; // mix(black, $primary, 80%)
$black: #222222;
$white: #eeeeee;

@import url(https://fonts.googleapis.com/css?family=Open+Sans:400|Raleway:300,700);
$open-sans: 'Open Sans', Helvetica, arial, sans-serif;
$raleway: 'Raleway', 'Open Sans', sans-serif;

$ease-in-out-cubic: cubic-bezier(0.645,  0.045, 0.355, 1.000);
$cubic-transition: 0.55s $ease-in-out-cubic;

$bar-length: 28px;
$bar-thickness: 4px;
$nav-item-delay: 0.05s;
$nav-width: 3.75rem;

.nav {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: $primary-ultradark;
    transition: 1s $ease-in-out-cubic;
    will-change: width;
    z-index: 999;
    
    &.is-active {
        width: 100%;
    }
}

.nav__trigger {
    display: block;
    position: absolute;
    top: 50%;
    right: 1rem;
    margin-top: -(($bar-thickness*5) / 2);
    padding: $bar-thickness*2 0;
    transition: 0.2s ease-in-out;
    // transform: translateY(-50%);
    // transform-origin: 100% 50%;
    z-index: 10;
    
    .bars {
        position: relative;
        
        &,
        &:before,
        &:after {
            width: $bar-length;
            height: $bar-thickness;
            background-color: $primary;
            border-radius: $bar-thickness;
            transition: 0.2s ease-in-out;
        }
        
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            will-change: transform;
        }

        &:before {
            transform: translateY(-($bar-thickness*2));
        }
        
        &:after {
            transform: translateY(($bar-thickness*2));
        }
    }
    
    &.is-active {
        transform: rotate(-45deg);
        
        .bars {            
            &:before,
            &:after {
                transform: translateX(0)
                           rotate(-90deg);
            }
        }
    }
}
.nav__content {
    display: none;
}
.nav__content li {
    list-style: none;
    padding: 5px 0;
}
.nav__content a {
    text-decoration: none;
    color: #fff;
    margin: 10px 10px;
}
.active {
    display: block;
    padding: 70px 0 20px 20px;
    background-color:  #161612;
}
}
.nav article {
    display: block;
    position: absolute;
    top: 17%;
    left: 1rem;
}
.nav article img {
    width: 80px;
}

.active-nav {
    border-bottom: 3px solid;
    border-bottom-color: #fff;
}

@media only screen and (min-width: 816px) {
    .nav {
        display: none;
    }
}
</style>